









import { Component, Vue } from 'vue-property-decorator';

@Component
export default class Login extends Vue {
  nickName: string = '';

  loginHandle() {
    if (!this.nickName) {
      return alert('Please type your nickname');
    }
    this.$store.dispatch('login', this.nickName);
    this.$router.replace({ name: 'home' }).catch(() => {});
  }
}
